@import url(https://fonts.googleapis.com/css?family=Inter);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
:root {
  --bg-gradient: linear-gradient(90deg, rgb(8,23,50) 0%, rgb(43,13,54) 100%);
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

p::selection {
  color: #FF80AB;
}

h1::selection {
  color: #FF80AB;
}

h2::selection {
  color: #FF80AB;
}

h3::selection {
  color: #FF80AB;
}

h4::selection {
  color: #FF80AB;
}

h5::selection {
  color: #FF80AB;
}

h6::selection {
  color: #FF80AB;
}

a::selection {
  color: #FF80AB;
}

Link::selection {
  color: #FF80AB;
}
.landing {
  background: var(--bg-gradient);
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  position: fixed;
  font-family: Inter;
}

.landing-photo {
  width:100vw;
  -webkit-clip-path: polygon(100% 0, 100% 100vh, 20% 0);
          clip-path: polygon(100% 0, 100% 100vh, 20% 0);
}

.landing-photo-wrap {
  -webkit-filter: drop-shadow(-15px 8px 10px #110011);
          filter: drop-shadow(-15px 8px 10px #110011);
}

.intro {
  top: 5%;
  left: 4.5vw;
  position: absolute;
  -webkit-align-items: center;
          align-items: center;
}

.img-container {
  width: 19%;
  height: auto;
  text-align: center;
  /* background-color: blue; */
}
.img-container img {
  width: 96%;
}

.intro h2 {
  width: 40%;
  font-weight: 800;
  color: #FADAFF;
  font-size: 5.5em;
  line-height: 1.1em;
  text-shadow: 0px 4px 4px rgba(0,0,0,0.25);
  margin: 0;
  margin-bottom: 15px;

}

.intro p {
  margin: 0;
  width: 50%;
  color: #F6CDF7;
  font-style: normal;
  font-size: 36px;
  line-height: 64px;
  font-weight: 300;
}

.butt-container {
  margin-left: -15px;
  margin-top: 20px;
  display: -webkit-flex;
  display: flex;
  z-index: 0;
}

.butt {
  margin-right: 90px;
  display: -webkit-flex;
  display: flex;
  background: linear-gradient(86.86deg, #FF80AB 0%, #EA80FB 100%), #FF80AB;
  box-shadow: 0px 4px 32px 1px rgba(0, 0, 0, 0.7);
  border-radius: 25px;
  -webkit-justify-content: center;
          justify-content: center;
  z-index: 2;
  transition: all 0.2s ease;
}

.butt a {
  font-weight: normal;
  font-style: normal;
  padding: 15px 25px 15px;
  color: white;
  font-size: 32px;
  line-height: 44px;
  text-decoration: none;
}

.butt:hover {
  cursor: pointer;
  background:  #c8457c;
  transition: all 0.4s ease;
}
.home {
  background: var(--bg-gradient);
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  font-family: Inter;
  font-size: 24px;
  font-style: normal;
}

.home-container {
  margin-top: 50px;
  width: 100%;
}

.home-section {
  display: block;
  margin: 125px;
}

h1 {
  color: #fadaff;
  font-weight: 800;
  font-size: 4em;
  line-height: 1.2em;
  margin-bottom: 0;
}

h2 {
  color: #fadaff;
  font-weight: 800;
  font-size: 3em;
  line-height: 1.2em;
  margin-bottom: 0;
}

p {
  color: #F6CDF7;
  font-weight: 300;
  font-size: 1.5em;
  line-height: 1.75em;

}

.dev-container {
  display: inline;
  margin: 0;
  width: 100%;
}

.dev-row {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.dev-card {
  z-index: 0;
  /* position: relative; */
  background: #FADAFF;
  box-shadow: 0px 4px 32px 1px rgba(0, 0, 0, 0.7);
  border-radius: 25px;
  width: 50%;
  height: 187px;
  margin: 25px;
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  transition: all 0.4s ease;
}

.dev-card:hover {
  -webkit-transform: scale(1.025);
          transform: scale(1.025);
}

.dev-photo {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.dev-photo img {
  width: 100%;
  margin-top: 10px;
}

.dev-info {
  z-index: 0;
  display: inline-block;
  width: 70%;
  padding: 20px;
  overflow: hidden;
}

.dev-info h3 {
  margin: 5px 0px;
  color: #061832;
  font-size: 1em;
  font-weight: 700;
  line-height: 1.2em;
}

.dev-info h4 {
  margin: 5px 0px;
  color: #753580;
  font-weight: 600;
  font-size: 0.583em;
  line-height: 1.2em;
}

.dev-info p {
  margin: 10px 0px;
  color: #753580;
  font-weight: 400;
  font-size: 0.583em;
  line-height: 1.2em;
}

.dev-links {
  width: auto;
}

.dev-links a {
  text-decoration: none;
  float: right;
  margin: 0.9em 0.5em 0 1.5em;
  color: #4C21D7;
  font-weight: 700;
  font-size: 0.583em;
  z-index: 1;

}

.dev-links a:hover {
  color:#753580;
  transition: all 0.5s ease;
  text-decoration: underline;
}

.home-butt-container {
  width: 32%;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  -webkit-align-items: center;
          align-items: center;

  margin: auto;
  margin-top: 6vw;
}

.home-butt {
  display: -webkit-flex;
  display: flex;
  background: linear-gradient(86.86deg, #FF80AB 0%, #EA80FB 100%), #FF80AB;
  box-shadow: 0px 4px 32px 1px rgba(0, 0, 0, 0.7);
  border-radius: 25px;
  -webkit-justify-content: center;
          justify-content: center;
  z-index: 2;
  transition: all 0.2s ease;
}

.home-butt a {
  font-weight: normal;
  font-style: normal;
  padding: 15px 25px 15px;
  color: white;
  font-size: 32px;
  line-height: 44px;
  text-decoration: none;
}

.home-butt:hover {
  cursor: pointer;
  background:  #c8457c;
  transition: all 0.4s ease;
}

@media screen and (min-width: 2560px) {
  .dev-photo {
    width: 8vw;
  }
}

@media screen and (max-width: 2560px) {
  .dev-photo {
    width: 8.2vw;
  }
}

@media screen and (max-width: 2400px) {
  .dev-photo {
    width: 9vw;
  }
}

@media screen and (max-width: 2240px) {
  .dev-photo {
    width: 10.5vw;
  }
}

@media screen and (max-width: 1920px) {
  .dev-photo {
    width: 11.5vw;
  }
}

@media screen and (max-width: 1800px) {
  .dev-photo {
    width: 12.7vw;
  }
}

@media screen and (max-width: 1680px) {
  .dev-photo {
    width: 15vw;
  }
}
.navbar {
  background: var(--bg-gradient);
  width: 100%;
  padding-top: 3px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  position: absolute;
  z-index: 1;
  height: 150px;
}

.navbar-container {
  width:100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
}

.logo-container {
  width: 50vw;
  height: 100%;
  margin-left: 50px;
  margin-right: 10px;
}

.logo {
  text-align: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100%;
  justify-self: start;
  display: -webkit-flex;
  display: flex;
}

.logo img {
  height: 70%;
}

.logo h2 {
  background-image: linear-gradient(90deg, #EA80FB, #FF80AB);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; 
  -moz-background-clip: text;
  -moz-text-fill-color: transparent;
  margin: 0;
  margin-top: -10px;
  display: inline-block;
  vertical-align: middle;
  font-size: 48px;
  font-weight: 900;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-gap: 15px;
  list-style: none;
  width: 50vw;
  margin-right: 40px;
  -webkit-justify-content: right;
          justify-content: right;
  justify-items: right;
  -webkit-align-content: right;
          align-content: right;
}

.nav-item {
  font-size: 22px;
  height: 35px;
  margin-left: 75px;
}

.nav-link {
  background-image: linear-gradient(90deg, #EA80FB, #FF80AB);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; 
  -moz-background-clip: text;
  -moz-text-fill-color: transparent;
  font-size: 36px;
  font-weight: 600;
  text-decoration: none;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  height: 100%;
  border-bottom: 0px solid var(--accent);

}

.nav-link:hover {
  /* color: #999; */
  border-bottom: 2px solid var(--accent);
  /* transition: all 0.1s ease-out; */
}
